//
// Component:       Variables
// Description:     Defines common values which are used across all components
//
// ========================================================================

// Breakpoints
// ========================================================================

$breakpoint-small: 640px !default;
$breakpoint-medium: 960px !default;
$breakpoint-large: 1200px !default;
$breakpoint-xlarge: 1600px !default;
$breakpoint-xsmall-max: ($breakpoint-small - 1) !default;
$breakpoint-small-max: ($breakpoint-medium - 1) !default;
$breakpoint-medium-max: ($breakpoint-large - 1) !default;
$breakpoint-large-max: ($breakpoint-xlarge - 1) !default;

// Global variables
// ========================================================================

//
// Typography
//
$global-font-family: 'Khula',
-apple-system,
BlinkMacSystemFont,
"Segoe UI",
Roboto,
"Helvetica Neue",
Arial,
"Noto Sans",
sans-serif,
"Apple Color Emoji",
"Segoe UI Emoji",
"Segoe UI Symbol",
"Noto Color Emoji" !default;
$global-font-size: 16px !default;
$global-line-height: 1.5 !default;
$global-2xlarge-font-size: 42px !default;
$global-xlarge-font-size: 32px !default;
$global-large-font-size: 24px !default;
$global-medium-font-size: 20px !default;
$global-small-font-size: 14px !default;
$global-xsmall-font-size: 12px !default;

//
// Colors
//
$global-color: #32477e !default;
$global-emphasis-color: #13254b !default;
$global-muted-color: #a3a3b0 !default;
$global-link-color: #25b5b4 !default;
$global-link-hover-color: #1a2980 !default;
$global-inverse-color: #fff !default;

//
// Backgrounds
//
$global-background: #eefbfb !default;
$global-muted-background: #fafbfc !default;
$global-primary-background: #26b9c4 !default;
$global-secondary-background: #0d1933 !default;

$global-success-background: #6dd989 !default;
$global-warning-background: #ffb237 !default;
$global-danger-background: #f35270 !default;

//
// Borders
//
$global-border-width: 1px !default;
$global-border: #ebebed !default;

//
// Box-Shadows
//
$global-small-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08) !default;
$global-medium-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.08) !default;
$global-large-box-shadow: 0 14px 25px rgba(0, 0, 0, 0.16) !default;
$global-xlarge-box-shadow: 0 28px 50px rgba(0, 0, 0, 0.16) !default;

//
// Margin
//
$global-margin: 20px !default;
$global-small-margin: 10px !default;
$global-medium-margin: 40px !default;
$global-large-margin: 70px !default;
$global-xlarge-margin: 140px !default;

//
// Gutters
//
$global-gutter: 30px !default;
$global-small-gutter: 15px !default;
$global-medium-gutter: 40px !default;
$global-large-gutter: 70px !default;

//
// Controls
//
$global-control-height: 40px !default;
$global-control-small-height: 30px !default;
$global-control-large-height: 55px !default;

//
// Z-index
//
$global-z-index: 1000 !default;

//
// Inverse
//
$inverse-global-color-mode: light !default;
$inverse-global-color: rgba($global-inverse-color, 0.7) !default;
$inverse-global-emphasis-color: $global-inverse-color !default;
$inverse-global-muted-color: rgba($global-inverse-color, 0.5) !default;
$inverse-global-inverse-color: $global-color !default;
$inverse-global-primary-background: $global-inverse-color !default;
$inverse-global-muted-background: rgba($global-inverse-color, 0.1) !default;
$inverse-global-border: rgba($global-inverse-color, 0.2) !default;

//@mixin hook-inverse(){}

// custom variables
$button-default-hover-gradient:  linear-gradient(320deg, $global-primary-background, #2691c4);
$button-default-active-gradient: linear-gradient(320deg, $global-primary-background, #2677c4);
$button-primary-hover-gradient: linear-gradient(320deg, $global-primary-background, #2684c4);
$button-secondary-active-gradient:  linear-gradient(320deg, #26acc4, #2677c4);
// component variables overrides
$base-link-hover-text-decoration: none;
$base-selection-color:#13254b;
$base-selection-background:#fffc16;

$heading-bullet-border:$global-primary-background;
$divider-small-border:$global-primary-background;
$divider-vertical-border:$global-primary-background;

$table-header-cell-color :$global-muted-color;
$table-row-active-background: #f7f8fa;

$icon-link-color: $global-emphasis-color;
$icon-link-hover-color: $global-primary-background;
$icon-link-active-color:#2b3d6c;
$icon-button-color: $global-emphasis-color;
$icon-button-hover-background: $global-primary-background;
$icon-button-hover-color: $global-inverse-color;
$icon-button-active-background: $global-secondary-background;
$icon-button-active-color: $global-inverse-color;

$button-default-background: $global-inverse-color;

$button-default-hover-background:$global-primary-background;
$button-default-hover-color: $global-inverse-color;
$button-default-active-background: #22a5af;
$button-default-active-color: $global-inverse-color;
$button-primary-active-background:$button-default-active-background;
$button-secondary-hover-background:$global-primary-background;
$button-secondary-active-background:$button-default-active-background;
$button-text-hover-color: $global-primary-background;
$button-link-hover-color:$global-primary-background;

$nav-default-sublist-item-hover-color:$global-primary-background;
$nav-default-sublist-item-active-color:$global-primary-background;
$nav-primary-item-color: $global-emphasis-color;
$nav-primary-item-hover-color:$global-primary-background;
$nav-primary-item-active-color:$global-primary-background;
$nav-primary-sublist-item-hover-color:$global-primary-background;
$nav-primary-sublist-item-active-color:$global-primary-background;
$navbar-toggle-color: $global-emphasis-color;

$navbar-toggle-hover-color: $global-primary-background;
$navbar-dropdown-background: $global-inverse-color;
$navbar-dropdown-nav-item-color: $global-emphasis-color;
$navbar-dropdown-nav-item-hover-color: $global-primary-background;
$navbar-dropdown-nav-item-active-color: $global-primary-background;
$navbar-dropdown-nav-sublist-item-color: $global-emphasis-color;
$navbar-dropdown-nav-sublist-item-hover-color: $global-primary-background;
$navbar-dropdown-nav-sublist-item-active-color: $global-primary-background;
$navbar-dropbar-background: $global-inverse-color;

$subnav-item-color: $global-emphasis-color;
$subnav-item-hover-color: $global-link-color;
$subnav-item-active-color:$subnav-item-hover-color;

$iconnav-item-hover-color: $global-primary-background;
$iconnav-item-active-color: $global-primary-background;
